

.topnav {
  display: flex;
  padding: 12px 20px;
  background-color: #41414173;
  border-bottom: 1px solid #ddd;
  font-family: 'Prompt', sans-serif;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.336);

}

.nav-link {
  margin-right: 1.5rem;
  text-decoration: none;
  color: #ffffff;
  font-weight: 600;
  transition: color 0.2s ease;
  margin-top: 5px;
}

.nav-link:hover {
  color: #003166;
}

.nav-link.active {
  color: #003166;
  border-bottom: 2px solid #003166;
  padding-bottom: 4px;
  font-weight: 700;
}

