.container-download {
    width: 75%;
    max-width: 600px;
    height: 520px;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    margin-top: 20px;
    justify-self: center;
    border-radius: 25px;
    box-shadow: 0px 20px 40px rgba(0, 0, 0, 0.15),
                0px 10px 20px rgba(0, 0, 0, 0.1);
    font-family: 'Prompt', sans-serif;
    position: relative;
    overflow: hidden;
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.2);
    transition: all 0.3s ease;
}

.container-download::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.05) 100%);
    border-radius: 25px;
    pointer-events: none;
}

.container-download:hover {
    transform: translateY(-5px);
    box-shadow: 0px 25px 50px rgba(0, 0, 0, 0.2),
                0px 15px 30px rgba(0, 0, 0, 0.15);
}

.container-download h3 {
    font-size: 28px;
    text-align: center;
    font-weight: 700;
    color: white;
    text-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
    border-bottom: 3px solid rgba(255, 255, 255, 0.3);
    padding-top: 25px;
    padding-bottom: 20px;
    margin: 0 30px;
    position: relative;
    z-index: 2;
    letter-spacing: 0.5px;
}

.container-download h3::after {
    content: '';
    position: absolute;
    bottom: -3px;
    left: 50%;
    transform: translateX(-50%);
    width: 60px;
    height: 3px;
    background: linear-gradient(90deg, #ff6b6b, #feca57);
    border-radius: 2px;
}

.container-download select {
    width: calc(100% - 60px);
    max-width: 400px;
    padding: 15px 20px;
    margin: 20px 30px 15px;
    border-radius: 12px;
    border: none;
    background: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(10px);
    font-family: 'Prompt', sans-serif;
    font-size: 16px;
    font-weight: 500;
    text-align: center;
    color: #333;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease;
    cursor: pointer;
    position: relative;
    z-index: 2;
}

.container-download select:hover {
    background: rgba(255, 255, 255, 1);
    transform: translateY(-2px);
    box-shadow: 0px 8px 25px rgba(0, 0, 0, 0.15);
}

.container-download select:focus {
    outline: none;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 0px 0px 3px rgba(255, 255, 255, 0.3),
                0px 8px 25px rgba(0, 0, 0, 0.15);
}

.container-download button {
    width: calc(100% - 60px);
    max-width: 400px;
    padding: 15px 25px;
    margin: 15px 30px;
    border-radius: 12px;
    border: none;
    background: linear-gradient(135deg, #ff6b6b 0%, #feca57 100%);
    color: white;
    font-family: 'Prompt', sans-serif;
    font-size: 18px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0px 8px 20px rgba(255, 107, 107, 0.3);
    text-transform: uppercase;
    letter-spacing: 1px;
    position: relative;
    z-index: 2;
    overflow: hidden;
}

.container-download button::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
    transition: left 0.5s ease;
}

.container-download button:hover {
    transform: translateY(-3px);
    box-shadow: 0px 12px 30px rgba(255, 107, 107, 0.4);
    background: linear-gradient(135deg, #ff5252 0%, #ffc107 100%);
}

.container-download button:hover::before {
    left: 100%;
}

.container-download button:active {
    transform: translateY(-1px);
    box-shadow: 0px 6px 15px rgba(255, 107, 107, 0.3);
}

/* เพิ่มเอฟเฟกต์พิเศษ */
.container-download::after {
    content: '';
    position: absolute;
    top: -50%;
    right: -50%;
    width: 100%;
    height: 100%;
    background: radial-gradient(circle, rgba(255, 255, 255, 0.1) 0%, transparent 70%);
    transform: rotate(45deg);
    pointer-events: none;
}

/* Responsive Design */
@media (max-width: 768px) {
    .container-download {
        width: 90%;
        height: auto;
        min-height: 450px;
        padding-bottom: 30px;
    }
    
    .container-download h3 {
        font-size: 24px;
        margin: 0 20px;
    }
    
    .container-download select,
    .container-download button {
        width: calc(100% - 40px);
        margin-left: 20px;
        margin-right: 20px;
    }
}