.question-editor-container {
  padding: 20px;
  font-family: 'Prompt', sans-serif;
}

.question-editor-title {
  font-size: 24px;
  margin-bottom: 20px;
}

.question-editor-title:hover {
    color: #012d5c;
    border-bottom: 2px solid #003166 ;
}

.question-editor-input {
  width: 100%;
  padding: 8px;
  margin-bottom: 20px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-family: 'Prompt', sans-serif;
}

.question-card {
  border: 1px solid #ddd;
  border-radius: 6px;
  padding: 12px;
  margin-bottom: 20px;
  background-color: #fafafa;
  font-family: 'Prompt', sans-serif;
}

.question-card label {
  display: block;
  font-weight: bold;
  margin-bottom: 4px;
  font-family: 'Prompt', sans-serif;
}

.question-card input,
.question-card select {
  width: 99%;
  padding: 6px;
  margin-bottom: 10px;
  border-radius: 4px;
  border: 1px solid #ccc;
  font-family: 'Prompt', sans-serif;
}

.dropdown-options {
  margin-top: 10px;
}

.dropdown-option {
  display: flex;
  gap: 10px;
  margin-bottom: 6px;
  
}

.dropdown-option input {
  flex: 1;
}

.button-group {
  margin-top: 20px;
}

.button-group button {
  padding: 8px 12px;
  font-size: 14px;
  margin-right: 10px;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  
}

.button-group button:hover {
  opacity: 0.85;
}

.add-button {
  background-color: #28a745;
  color: white;
  font-family: 'Prompt', sans-serif;
}

.save-button {
  background-color: #007bff;
  color: white;
  font-family: 'Prompt', sans-serif;
}

.select-drop {
    width: 50%;
}

.add-button-aa {
  background-color: #28a745;
  color: white;
  font-family: 'Prompt', sans-serif;
  border-radius: 5px;
  border: none;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.459);

}

.add-button-aa:hover {
    background-color: #26833a;
    transform: scale(0.95);
}

.btn-delete-q {
  background-color: #f56161;
  border: none;
  padding: 5px;
  border-radius: 5px;
  width: 120px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.322);
}

.btn-delete-q:hover {
  background-color: #720303;
  color: #ddd;
}