/* styles.css */
.edit-panel {
  width: 250px;
  border-left: 1px solid #ccc;
  height: auto;
  padding: 16px;
  overflow-y: auto;
  background-color: #fafafa;
  font-family: 'Prompt', sans-serif;
}

.edit-panel h4 {
  margin-bottom: 16px;
  color: #333;
}

.no-node-msg {
  font-style: italic;
  color: #888;
}

.form-group {
  margin-bottom: 16px;
}

.form-group label {
  display: block;
  font-weight: bold;
  margin-bottom: 6px;
}

.form-group input,
.form-group select {
  width: 95%;
  padding: 6px 10px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 14px;
}

.dropdown-options {
  margin-top: 20px;
}

.dropdown-item {
  margin-bottom: 12px;
}

.dropdown-item input {
  width: 95%;
  padding: 6px 10px;
  border: 1px solid #bbb;
  border-radius: 6px;
}

.hint-text {
  display: block;
  color: #777;
  font-size: 12px;
}

.button-group {
  margin-top: 24px;
}

.btn-primary {
  width: 100%;
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: bold;
  margin-bottom: 10px;
  font-family: 'Prompt', sans-serif;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.329);
}

.btn-primary:hover {
  background-color: #0056b3;
}

.btn-danger {
  width: 100%;
  padding: 10px;
  background-color: #e53935;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: bold;
  font-family: 'Prompt', sans-serif;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.329);
}

.btn-danger:hover {
  background-color: #b71c1c;
}

.btn-add {
  width: 100%;
  margin-top: 10px;
  background-color: #4caf50;
  color: white;
  border: none;
  padding: 8px 14px;
  margin-bottom: 10px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  transition: background 0.2s ease;
  font-family: 'Prompt', sans-serif;
  font-weight: bold;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.329);
}
.btn-add:hover {
  background-color: #43a047;
}


