.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup-box {
  background: white;
  padding: 24px;
  border-radius: 8px;
  width: 600px;
  max-height: 80vh;
  overflow-y: auto;
}

.popup-box h3 {
  margin-top: 0;
}

.popup-box input[type="text"] {
  width: 100%;
  padding: 8px;
  margin: 8px 0 16px;
}

.question-list {
  max-height: 300px;
  overflow-y: auto;
  border: 1px solid #ccc;
  padding: 8px;
  margin-bottom: 16px;
}

.question-item {
  display: block;
  margin-bottom: 6px;
}

.popup-actions {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}
